.footer {
    padding-top: 73px;
    padding-bottom: 102px;
    background-color: $black;
    color: #fff;
    position: relative;
    @include r($tablet) {
        min-height: 1000px; }
    @include r($mob-m) {
        min-height: 1200px; }
    @include r(528) {
        min-height: 1300px; }
    &__wrapper {
        padding: 0 40px; }
    &__in {
        width: percentage(625/1000);
        display: inline-block;
        padding-left: 70px;
        vertical-align: top;
        @include r($tablet) {
            width: 40%; }
        @include r($mob-l) {
            padding-left: 50px; }
        @include r($mob-m) {
            width: 100%;
            margin-top: 40px; }
        @include r(528) {
            text-align: center;
            padding-left: 0; } }
    &__col {
        display: inline-block;
        width: percentage(300/625);
        vertical-align: top;
        @include r($tablet) {
            display: block;
            width: 100%; }
        @include r($mob-m) {
            display: inline-block; } }
    &__ttl {
        @include helv-th;
        font-size: 24px;
        font-weight: 400;
        text-transform: uppercase;
        margin-bottom: 22px;
        line-height: 1; }
    &__bl {
        margin-bottom: 15px;
        @include r($tablet) {
            margin-bottom: 35px; }
        @include r($mob-m) {
            display: inline-block;
            padding-right: 35px; }
        @include r(528) {
            display: block;
            padding-right: 0; } }
    &__subttl {
        @include helv-m;
        font-size: 14px;
        font-weight: 400;
        color: $gold; }
    &__contacts {
        margin-top: 40px;
        @include r($mob-m) {
            margin-top: 0; } } }

.facebook-widget {
    display: inline-block;
    @include r($mob-m) {
        display: block;
        text-align: center; } }

