.func {
    @include clr;
    &__btn {
        float: left;
        width: 50%;
        min-height: 350px;
        @include css-lock(100, 350, 320, 1920, min-height);
        color: #fff;
        @include cover;
        &:hover {
            opacity: 0.9; }
        @include r($mob-l) {
            width: 100%; } }
    &__ttl {
        @include helv-th;
        font-size: 24px;
        text-transform: uppercase;
        font-weight: 400;
        padding: 0 20px; }
    &__txt {
        @include helv-m;
        padding: 0 20px; } }


.gallery {
    max-height: 100vh;
    &__slide {
        width: 100%;
        height: 100vh;
        @include cover; }
    &__btn {
        background-color: transparent;
        z-index: 1000;
        right: percentage(95/1920);
        top: percentage(78/1920);
        @include r($tablet) {
            transform: scale(0.8); }
        @include r($mob-l) {
            transform: scale(0.5);
            right: 0; }
        &_next {
            right: percentage(95/1920);
            @include r($mob-l) {
                right: 0; } }
        &_prev {
            left: percentage(95/1920);
            @include r($mob-l) {
                left: 0; } } }
    &__arr {
        background-color: transparent;
        position: absolute;
        bottom: percentage(85/1920);
        opacity: 1;
        z-index: 1000;
        &:hover {
            opacity: 0.8; }
        @include r($tablet) {
            transform: scale(0.8); }
        @include r($mob-l) {
            transform: scale(0.5); } } }
.gallery-popup {
    width: 100%; }
.webcam-popup {
    width: 857px;
    @include r($tablet) {
        width: 720px; }
    @include r($mob-l) {
        width: 565px; }
    @include r($mob-m) {
        width: 400px; }
    @include r($mob-s) {
        width: 310px; }
    &__video {
        max-width: 100%;
        appearance: none; } }



.icon-arrow-left {
    @include s(arrow-left);
    display: block; }
.icon-arrow-right {
    @include s(arrow-right);
    display: block; }
.icon-cancel-white {
    @include s(cancel-white);
    display: block; }

