.apart {
    padding-top: 110px;
    padding-bottom: 130px;
    position: relative;
    @include r($tablet) {
        padding-top: 50px;
        padding-bottom: 50px; }
    &__wrapper {
        padding: 0 20px; }
    &__col {
        display: inline-block;
        &_l {
            width: percentage(230/1000);
            vertical-align: top;
            @include r($tablet) {
                display: block;
                width: 100%; } }
        &_m {
            width: percentage(425/1000);
            vertical-align: bottom;
            @include r($tablet) {
                width: 60%; }
            @include r($mob-l) {
                width: 100%; } }
        &_r {
            width: percentage(330/1000);
            vertical-align: top;
            text-align: right;
            @include r($tablet) {
                width: 40%;
                text-align: left;
                padding-left: 50px; }
            @include r($mob-l) {
                width: 100%;
                text-align: center;
                padding-left: 0; } } }
    &__mini-col {
        display: inline-block;
        &_l {
            text-align: left;
            width: percentage(145/230); }
        &_r {
            text-align: right;
            width: percentage(80/230); } }
    &__ttl {
        @include helv-r;
        color: $gold;
        font-size: 18px;
        text-transform: uppercase;
        margin-top: 42px;
        margin-bottom: 35px;
        @include r($tablet) {
            display: inline-block;
            vertical-align: middle;
            margin-right: 10%; } }
    &__types {
        margin-bottom: 110px;
        @include r($tablet) {
            display: inline-block;
            vertical-align: middle;
            margin-bottom: 0;
            margin-right: 10%; } }
    &__type {
        display: block;
        background-color: transparent;
        @include helv-m;
        color: $gray;
        text-decoration: underline;
        line-height: 2;
        &:hover, &.is-active {
            color: $gold; }
        @include r($tablet) {
            display: inline-block;
            margin: 0 20px; }
        @include r($mob-s) {
            display: block;
            margin: 0 15px; } }
    &__txt {
        max-width: 150px;
        font-size: 12.6px;
        @include r($tablet) {
            display: inline-block;
            vertical-align: middle;
            max-width: 33%; }
        @include r($mob-l) {
            max-width: 100%; } }
    &__metrics {
        display: inline-block;
        padding-top: 48px;
        padding-bottom: 30px;
        min-width: 100%;
        font-size: 12px;
        display: none;
        &.is-active {
            display: block; } }
    &__rooms {
        li {
            list-style-type: decimal-leading-zero;
            list-style-position: inside; } }
    &__total {
        margin-top: 30px; }
    &__links {
        @include helv-m;
        font-size: 14px; }
    &__link {
        display: block;
        line-height: 2.3;
        text-align: left;
        &_red {
            color: $red; }
        &_black {
            color: $black; } } }

.apart-menu {
    &__item {
        display: inline-block;
        margin-right: 40px;
        @include r(492) {
            margin-right: 0; } }
    &__ttl {
        padding: 15px 0; }
    &__btn {
        background-color: transparent;
        border: 1px solid $gray;
        border-radius: 1px;
        height: 40px;
        margin-right: 6px;
        @include helv-m;
        color: $gray;
        &:hover, &.is-active {
            color: #fff;
            background-color: $gray; }
        @include r(492) {
            margin-bottom: 15px; }
        &_lg {
            width: 150px; }
        &_sm {
            width: 50px; } } }


.apart-content {
    display: none;
    font-size: 0;
    &.is-active {
        display: block; }
    &__in {
        display: inline-block;
        min-width: 230px; }
    &__desc {
        display: none;
        &.is-active {
            display: block; } }
    &__img {
        width: 100%;
        cursor: pointer;
        display: none;
        img {
            max-width: 100%; }
        &.is-active {
            display: block; } } }


.apart-popup {
    width: 100%;
    height: 100vh;
    background-color: #fff;
    @include vertical;
    &__in {
        display: inline-block;
        vertical-align: middle;
        @include vertical;
        text-align: left;
        padding: 40px;
        @include r($tablet) {
            padding-right: 105px;
            text-align: center; }
        @include r($mob-l) {
            padding: 0; } }
    &__img {
        display: inline-block;
        width: percentage(1153/1920);
        vertical-align: middle;
        @include r($tablet) {
            width: 80%; }
        img {
            max-width: 100%; } }
    &__txt {
        display: inline-block;
        width: percentage(767/1920);
        min-width: 320px;
        vertical-align: middle;
        padding-left: percentage(110/1920);
        font-size: 18px;
        @include r($tablet) {
            width: 50%;
            text-align: center;
            padding-left: 0; } }


    &__ttl {
        font-size: 24px;
        margin: 0; }
    &__type {
        @include helv-r;
        color: $gold;
        font-size: 24px;
        text-transform: uppercase;
        text-decoration: none;
        line-height: 1.4;
        @include r($tablet) {
            margin: 0; } }
    &__metrics {
        display: inline-block;
        font-size: 18px;
        padding-top: 30px;
        @include r($desktop-s) {
            font-size: 16px; }
        @include r($tablet) {
            font-size: 14px; } }
    &__total {
        @include helv-m;
        color: #b8aa86; }
    &__link {
        @include helv-m;
        font-size: 18px;
        @include r($desktop-s) {
            font-size: 16px; } } }
.icon-cancel-gold {
    @include s(cancel-gold);
    display: block; }



